@import '../../global.scss';

.topbar {
    width: 100%;
    height: 70px;
    background-color: $paleWhite;
    color: $mainColor;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    @include smallSize{
        height: 60px;
    }

    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .right {
            display: flex;
            align-items: center;

            .logo {
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;

                @include smallSize{
                    font-size: 28px;
                }
            }



            // .item-container {
            //     display: flex;
            //     align-items: center;
            //     margin-left: 30px;

            //     .icon {
            //         font-size: 30px;
            //         margin-right: 5px;
            //     }

            //     // span {
            //     //     font-size: 15px;
            //     //     font-weight: 500px;
            //     // }

            //     a {
            //         // font-size: inherit;
            //         color: inherit;
            //         text-decoration: none;
            //     }
            // }
        }

        .left {
            .hamburger {
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;

                @include smallSize{
                    width: 30px;
                    height: 20px;
                }

                span {
                    width: 100%;
                    height: 3px;
                    background-color: $mainColor;
                    transition: all 2s ease;
                }
            }
        }

    }

    &.active {
        background-color: $mainColor;
        color: $white;

        .left {
            .hamburger {
                span {
                    background-color: $white;
                }
            }
        }
    }
}